import React from "react";
import UserInput from "./UserInput";
import {bitwiseAdder, shiftLeft, toLen} from "../util/BitwiseHelpers";
import RegInfo from "./RegInfo";
import Paper from "@material-ui/core/Paper/Paper";
import Log from "./Log";
import Actions from "./Actions";
import {prepend} from "ramda";
const initialState = {
  a: "",
  b: "",
  reg1: "",
  reg2: "",
  reg3: "",
  reg4: "0",
  counter: "0000",
  userInputVisible: true,
  nextStep: "y1",
  log: [],
  programDone: false
};

class AppContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = initialState;
  }


  handleChange = (prop, value) => {
    this.setState({
      [prop]: value
    })
  };

  handleSubmitInput = (override = {}) => {
    this.setState({
      a: initialState.a,
      b: initialState.b,
      reg1: toLen(this.state.a),
      reg2: toLen(this.state.b),
      reg3: toLen(""),
      reg4: initialState.reg4,
      counter: initialState.counter,
      userInputVisible: false,
      nextStep: initialState.nextStep,
      log: initialState.log,
      programDone: initialState.programDone,
      ...override
    })
  };

  currentRegStateAsString = () =>
    " Reg1: " + this.state.reg1 + ", Reg2: " + this.state.reg2 + ", Reg3: " + this.state.reg3
    + ", Reg4: " + this.state.reg4 + ", Loendur: " + this.state.counter;

  addToLog2 = (newEntry) => {
    this.setState({
      log: prepend(newEntry + ": " + this.currentRegStateAsString(), this.state.log)
    });
  };

  addToLog = (newEntry) => {
    switch (newEntry) {
      case "y1":
        this.setState({ log: prepend("y1: Loendur: " + this.state.counter, this.state.log)});
        break;
      case "y2":
        this.setState({ log: prepend("y2: Register4: " + this.state.reg4, this.state.log)});
        break;
      case "y3":
        this.setState({ log: prepend("y3: Register3: " + this.state.reg3, this.state.log)});
        break;
      case "y4":
        this.setState({ log: prepend("y4: Register2: " + this.state.reg2, this.state.log)});
        break;
      case "y5":
        this.setState({ log: prepend("y4: Loendur: " + this.state.counter, this.state.log)});
        break;
      case "y6":
        this.setState({ log: prepend("y4: Register3: " + this.state.reg3, this.state.log)});
        break;
      case "x1":
        this.setState({ log: prepend("x1: --- UUE TSÜKLI ALGUS ---", this.state.log)});
        break;
      case "x2":
        this.setState({ log: prepend("x2: Ok", this.state.log)});
        break;
      case "x3":
        this.setState({ log: prepend("x3: Ok", this.state.log)});
        break;
      default:
        break;
    }
  };

  x1 = () => {
    if (this.state.reg2.charAt(0) === "0") {
      this.setState({ nextStep: "y4"}, () => this.addToLog("x1"))
    } else {
      this.setState({ nextStep: "y2"}, () => this.addToLog("x1"))
    }
  };

  x2 = () => {
    if (this.state.reg4 === "1" && this.state.reg3.charAt(14) !== "1") {
      this.setState({
        programDone: true,
        nextStep: "",
        log: prepend("x2: OVERFLOW ERROR", this.state.log),
        actionsDisabled: false
      })
    } else {
      this.setState({
        nextStep: "y4"
      }, () => this.addToLog("x2"))
    }
  };

  x3 = () => {
    if (this.state.counter === "0000") {
      this.setState({
        programDone: true,
        nextStep: "",
        actionsDisabled: false,
        log: prepend("x3: PROGRAMM LÕPETAS, VASTUS: " + this.state.reg3 + ", alusel 10:  "
          + parseInt(this.state.reg3, 2).toString(), this.state.log)
      })
    } else {
      this.setState({
        nextStep: "y6"
      }, () => this.addToLog("x3"))
    }
  };

  subtractCounter = (counter) => {
    return toLen((parseInt(counter, 2) - 1).toString(2), 4);
  };

  solve = () => {
    switch (this.state.nextStep) {
      case "y1":
        this.setState({
          nextStep: "x1",
          counter: "1110"
        }, () => this.addToLog("y1"));
        break;
      case "y2":
        this.setState({
          nextStep: "y3",
          reg4: this.state.reg3.charAt(0),
        }, () => this.addToLog("y2"));
        break;
      case "y3":
        this.setState({
          nextStep: "x2",
          reg3: bitwiseAdder(this.state.reg3, this.state.reg1),
        }, () => this.addToLog("y3"));
        break;
      case "y4":
        this.setState({
          reg2: shiftLeft(this.state.reg2),
          nextStep: "y5"
        }, () => this.addToLog("y4"));
        break;
      case "y5":
        this.setState({
          counter: this.subtractCounter(this.state.counter),
          nextStep: "x3"
        }, () => this.addToLog("y5"));
        break;
      case "y6":
        this.setState({
          reg3: shiftLeft(this.state.reg3),
          nextStep: "x1"
        }, () => this.addToLog("y6"));
        break;
      case "x1":
        this.x1();
        break;
      case "x2":
        this.x2();
        break;
      case "x3":
        this.x3();
        break;
      default:
        break;
    }
  };
  reset = () => this.setState(initialState);

  solveAll = () => {
    this.solve();
    if (!this.state.programDone) {
      setTimeout(this.solveAll, 500);
    }
  };

  solveFast = () => {
    this.solve();
    if (!this.state.programDone) {
      setTimeout(this.solveFast, 25);
    }
  };

  render() {
    if (this.state.userInputVisible) {
      return (
        <UserInput
          a={this.state.a}
          b={this.state.b}
          onChange={this.handleChange}
          handleSubmitInput={this.handleSubmitInput}
        />
      )
    }
    return (
      <Paper style={{width: "50%", marginLeft: "25%", marginTop: "5%", padding: 16}}>
        <Actions
          solveAll={() => { this.solveAll(); this.setState({actionsDisabled: true });}}
          solveNext={this.solve}
          actionsDisabled={this.state.actionsDisabled}
          programEnded={this.state.programDone}
          reset={this.reset}
          solveFast={() => { this.solveFast(); this.setState({actionsDisabled: true });}}
        />
        <RegInfo
          reg1={this.state.reg1}
          reg2={this.state.reg2}
          reg3={this.state.reg3}
          reg4={this.state.reg4}
          counter={this.state.counter}/>
        <Log log={this.state.log}/>
      </Paper>
    );
  }

}

export default AppContainer;