import React from 'react';
import PropTypes from 'prop-types';
import Paper from "@material-ui/core/Paper/Paper";
import Typography from "@material-ui/core/Typography/Typography";

const RegInfo = props => (
  <Paper>
    <Typography>
      Register1: {props.reg1}
    </Typography>
    <Typography>
      Register2: {props.reg2}
    </Typography>
    <Typography>
      Register3: {props.reg3}
    </Typography>
    <Typography>
      Register4: {props.reg4}
    </Typography>
    <Typography>
      Loendur: {props.counter}
    </Typography>
  </Paper>
);

export default RegInfo;

RegInfo.propTypes = {
  reg1: PropTypes.string.isRequired,
  reg2: PropTypes.string.isRequired,
  reg3: PropTypes.string.isRequired,
  reg4: PropTypes.string.isRequired,
  counter: PropTypes.string.isRequired
};