import React from 'react';
import PropTypes from 'prop-types';
import TextField from "@material-ui/core/TextField/TextField";
import Grid from "@material-ui/core/Grid/Grid";
import Paper from "@material-ui/core/Paper/Paper";
import Typography from "@material-ui/core/Typography/Typography";
import Button from "@material-ui/core/Button/Button";

class UserInput extends React.Component {

  handleChange = (prop, value) => {
    if ((!value || value.match(/^[0-1]*$/g)) && value.length < 15) {
      this.props.onChange(prop, value);
    }
  };

  preSelect71_91 = () => {
    this.props.handleSubmitInput({ reg1: "00000001000111", reg2: "00000001011011"});
  };

  preSelect30_112 = () => {
    this.props.handleSubmitInput({ reg1: "00000000011110", reg2: "00000001110000"});
  };

  render() {
    const button = { margin: 8 };
    return (
      <Paper style={{width: "30%", marginLeft: "40%", marginTop: "15%", padding: 16}}>
        <Grid container justify={"center"} alignItems={"flex-end"} spacing={16}>
          <Grid item xs={5}>
            <TextField
              id="standard-name"
              label="A"
              value={this.props.a}
              onChange={e => this.handleChange("a", e.target.value)}
              margin="normal"
            />
          </Grid>
          <Grid item xs={2}>
            <Typography variant={"h4"}>
              X
            </Typography>
          </Grid>
          <Grid item xs={5}>
            <TextField
              id="standard-name"
              label="B"
              value={this.props.b}
              onChange={e => this.handleChange("b", e.target.value)}
              margin="normal"
            />
          </Grid>
        </Grid>
        <Button
          style={button}
          variant={"contained"}
          onClick={this.props.handleSubmitInput}
          disabled={!this.props.a || !this.props.b}
        >
          Kinnita
        </Button>
        <Button
          variant={"contained"}
          onClick={this.preSelect71_91}
          style={button}
        >
          71 x 91
        </Button>
        <Button
          variant={"contained"}
          onClick={this.preSelect30_112}
          style={button}
        >
          30 X 112
        </Button>
      </Paper>
    );
  }
}

export default UserInput;

UserInput.propTypes = {
  a: PropTypes.string.isRequired,
  b: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  handleSubmitInput: PropTypes.func.isRequired
};