import {clone} from "ramda";

export const toLen = (bitwiseNumber, to = 14) => {
  return clone(bitwiseNumber).padStart(to, "0");
};

export const shiftLeft = bitwiseNumber => {
  return clone(bitwiseNumber).substring(1, 14) + "0";
};

export const bitwiseAdder = (a, b) => {
  return toLen((parseInt(a, 2) + parseInt(b, 2)).toString(2));
};