import React from 'react';
import PropTypes from 'prop-types';
import Button from "@material-ui/core/Button/Button";

const Actions = props => {
  const button = { margin: 8 };
  return (
    <div>
      <Button
        style={button}
        variant={"contained"}
        disabled={props.programEnded || props.actionsDisabled}
        onClick={props.solveNext}
      >
        Samm-sammu haaval
      </Button>
      <Button
        variant={"contained"}
        style={button}
        disabled={props.programEnded || props.actionsDisabled}
        onClick={props.solveAll}
      >
        Lahenda korraga
      </Button>
      <Button
        variant={"contained"}
        style={button}
        disabled={props.programEnded || props.actionsDisabled}
        onClick={props.solveFast}
      >
        Lahenda korraga ja kiiresti
      </Button>
      <Button
        onClick={props.reset}
        style={button}
        variant={"contained"}
        disabled={props.actionsDisabled}
      >
        Alusta uuesti
      </Button>
    </div>
  );
};

export default Actions;

Actions.propTypes = {
  solveAll: PropTypes.func.isRequired,
  solveNext: PropTypes.func.isRequired,
  actionsDisabled: PropTypes.bool.isRequired,
  programEnded: PropTypes.bool.isRequired,
  reset: PropTypes.func.isRequired,
  solveFast: PropTypes.func.isRequired
};